import { Model } from './Model';
import { Attributes } from '../attributes/Attributes';
import { Events } from '../events/Events';
export class ExTranscriptModel extends Model {
    static build(sStatusBase) {
        return new ExTranscriptModel(new Attributes(sStatusBase), new Events());
    }
}
export class SubtitleModel extends Model {
    static build(sSubtitlesBase) {
        return new SubtitleModel(new Attributes(sSubtitlesBase), new Events());
    }
}
