export class Model {
    constructor(attributes, events) {
        this.attributes = attributes;
        this.events = events;
    }
    get get() {
        return this.attributes.get;
    }
    get on() {
        return this.events.on;
    }
    get trigger() {
        return this.events.trigger;
    }
    set(prop) {
        this.attributes.set(prop);
        // NOTE: DO PASS prop
        this.events.trigger('change', prop);
    }
}
